	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/targeting-multiple-browsers-with-css-and-some-javascript-trickery/index.md absPath of file >>> MarkdownRemark","html":"<p>There's a clever little javascript library that allows you to write CSS specifically for Chrome, the iPhone, IE 8, etc. all in a single file.  What it does is manipulate the HTML document client-side to add additional classes to the page, based on your browser type.  The end result is that your page gets additional selectors like .ie or .gecko added - and you can setup a single CSS file to target all of these browsers in one shot - no server side magic or conditional comments in the HTML.</p>\n<p>You can find more information here ( <a href=\"http://snipplr.com/view/9340/css-browser-selector/\">http://snipplr.com/view/9340/css-browser-selector/</a> ) or download it from here (<a href=\"http://github.com/rafaelp/css_browser_selector\">http://github.com/rafaelp/css_browser_selector</a> ).</p>","frontmatter":{"title":"Targeting multiple browsers with CSS... and some Javascript trickery.","date":"November 28, 2009","tags":["css","javascript"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/embossed-shadowed-text-in-flex/","tags":["flex"],"title":"Embossed / Shadowed text in Flex","date":"2009-11-09T08:00:38.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/automating-tasks-in-flex-builder-with-the-help-of-a-little-something-called-ant/","tags":["flex","ant","eclipse","flex-builder","ftp"],"title":"Automating tasks in Flex Builder, with the help of a little something called 'ANT'.","date":"2009-12-04T09:00:57.000Z","description":null,"image":null}}}}

